.c-btn{@apply .px-4 .py-2 .rounded .bg-grey-lighter;}.c-btn:not(.outline){-webkit-box-shadow:0 2px 6px 0 rgba(0,0,0,.2);box-shadow:0 2px 6px 0 rgba(0,0,0,.2)}.c-btn:hover{@apply .cursor-pointer .bg-grey;}.c-btn:active{@apply .cursor-pointer .bg-grey-dark;}.c-btn.block{@apply .w-full;}.c-btn.large{@apply .text-lg .py-3;}.c-btn.large{@apply .text-lg .py-3;}.c-btn.danger:not(.outline) .c-icon,.c-btn.info:not(.outline) .c-icon,.c-btn.primary:not(.outline) .c-icon,.c-btn.success:not(.outline) .c-icon,.c-btn.warning:not(.outline) .c-icon{fill:#fff}.c-btn.primary{@apply .bg-blue .text-white;}.c-btn.primary:hover{@apply .bg-blue-dark;}.c-btn.primary:active{@apply .bg-blue-darker;}.c-btn.primary .c-icon{fill:#3490dc}.c-btn.warning{@apply .bg-orange .text-white;}.c-btn.warning:hover{@apply .bg-orange-dark;}.c-btn.warning:active{@apply .bg-orange-darker;}.c-btn.warning .c-icon{fill:#f6993f}.c-btn.success{@apply .bg-green .text-white;}.c-btn.success:hover{@apply .bg-green-dark;}.c-btn.success:active{@apply .bg-green-darker;}.c-btn.success .c-icon{fill:#38c172}.c-btn.danger{@apply .bg-red .text-white;}.c-btn.danger:hover{@apply .bg-red-dark;}.c-btn.danger:active{@apply .bg-red-darker;}.c-btn.danger .c-icon{fill:#e3342f}.c-btn.info{@apply .bg-teal .text-white;}.c-btn.info:hover{@apply .bg-teal-dark;}.c-btn.info:active{@apply .bg-teal-darker;}.c-btn.info .c-icon{fill:#4dc0b5}.c-btn[disabled]{@apply .opacity-50 .cursor-default;}.c-btn.outline{@apply .bg-transparent .border .border-solid;}.c-btn.outline.primary{@apply .border-blue .text-blue;}.c-btn.outline.warning{@apply .border-orange .text-orange;}.c-btn.outline.success{@apply .border-green .text-green;}.c-btn.outline.danger{@apply .border-red .text-red;}.c-btn.outline.info{@apply .border-teal .text-teal;}.c-btn.loading{@apply .relative;}.c-btn.loading.danger .loading-indicator>.animation,.c-btn.loading.info .loading-indicator>.animation,.c-btn.loading.primary .loading-indicator>.animation,.c-btn.loading.success .loading-indicator>.animation,.c-btn.loading.warning .loading-indicator>.animation{border-right-color:#fff;border-bottom-color:#fff}.c-btn.loading .loading-indicator{@apply .absolute;top:50%;left:50%;margin-top:-12px;margin-left:-12px}.c-btn.loading .c-btn-content{@apply .invisible;}.c-btn svg{@apply .mr-1;}.c-btn .c-btn-content{@apply .inline-block;}